.b-burger {
  display: none;
  border: none;
  background: transparent;
  box-shadow: none;
  width: 32px;
  height: 20px;
  position: relative;
  transform: rotate(0);
  transition: .5s ease-in-out;
  cursor: pointer;
}

.b-burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  border-radius: 9px;
  transform: rotate(0);
  transition: .25s ease-in-out;
  opacity: 1;
  left: 0;
}

.b-burger span:nth-child(1) {
  top: 0;
  transform-origin: center center;
}

.b-burger span:nth-child(2) {
  top: 50%;
  transform-origin: left center;
}

.b-burger span:nth-child(3) {
  top: 100%;
  transform-origin: center center;
}

.b-burger--active span:nth-child(1) {
  transform: translate(0, -50%) rotate(45deg);
  top: 50%;
}

.b-burger--active span:nth-child(2) {
  width: 0;
  opacity: 0;
}

.b-burger--active span:nth-child(3) {
  transform: translate(0, -50%) rotate(-45deg);
  top: 50%;
}

.b-button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 50px;
  width: 300px;
  height: 60px;
  transition: all .3s;
  border: none;
  box-shadow: none;
  background: white;
}

.b-button__content {
  color: #000;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .b-button__content {
    font-size: .7rem;
  }
}
