html {
  font-size: 16px;
}

body {
  font-family: "Open Sans", sans-serif !important;
  font-size: 1rem;
}

a {
  text-decoration: none !important;
  transition: color .3s;
}

* {
  outline: none;
  box-sizing: border-box;
}

img {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

textarea {
  resize: none;
}
